import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  cpfFormatter,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../dependencies/empresaMLFDependency';
import { CLASS_NAME, SociedadeUniProfissional } from '../../../../models/dtos/mlf/sociedadeUniProfissional';
import { INDICADOR_HABILITACAO_PROF_ENUM_VALUES } from '../../../../models/enumerated/mlf/indicadorHabilitacaoProfEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'nome',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const SociedadeUniProfissionalSearchView: FC<SearchViewProps<SociedadeUniProfissional>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'cpf',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cpfFormatter,
      sort: true,
      text: 'CPF',
    },
    {
      dataField: 'nome',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Profissional',
    },
    enumColumn(
      {
        dataField: 'indicadorHabilitacao',
        editor: true,
        filter: true,
        sort: true,
        text: 'Indicador Habilitação',
      },
      INDICADOR_HABILITACAO_PROF_ENUM_VALUES
    ),
  ];
  return (
    <WindowComponent
      title="Manutenção de Sociedade Uniprofissional - Empregados e Sócios"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'SociedadeUniProfissionalEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default SociedadeUniProfissionalSearchView;
